.image-app {
  margin: 0 auto;
  padding: 1px 1px 1px 1px;
  width: 100%;
  background-color: white;
}

.product-container {
  width: 100%;
  margin-bottom: 1px;
  border: 1px solid #c0c0c0;
  background-color: #ffffff;
  padding: 1px 1px 1px 1px;
}

.all-images {
	
}

.all-images > img {
	background-color: white;
	width : 75px;
	height : 75px;
	border: 2px solid #333;
	padding: 5px;
}

.product-variations {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.product-variations-label {
	flex: 20%;
}
.all-variations {
	flex: 80%;
	display: flex;
	flex-wrap: wrap;
}


.product-variations > div {
	background-color: white;
	border: 2px solid #333;
	padding: 5px;
}


.flex {
  display: flex;
  flex-wrap: wrap;
  width : 100%;
  border: 2px solid #333;
  align-content: stretch;
}

.glass-magnifier {
  width: 100%;
  position: relative;
}

.product-container h2 {
  font-size: 1.5rem;
  background-color: #333;
  color: #fff;
  padding: 1px 1px;
  text-align: center;
  font-weight: normal;
}

