.sitenav {
    height: 100%;
    width: 400px;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: white;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sitenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 20px;
    color: #0e0505;
    display: block;
    transition: 0.3s;
}

.sitenav a:hover {
    color: #ebd50f;
}

.sitenav .closebtn {
    position: relative;
    top: 0;
    right: 25px;
    font-size: 46px;
    margin-left: 50px;
}


@media screen and (max-height: 450px) {
    .sitenav {
        padding-top: 15px;
    }

    .sitenav a {
        font-size: 18px;
    }
}

.footer-h3 {color:  #0f0f0f;font: normal 36px 'Cookie', cursive;margin: 0;}
