@media (max-width: 880px) {

  .frame-container .market-comparision,
  .frame-container .market-place,
  .frame-container .amazon-container {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }
}

.frame-container {
	display: flex;
	flex-direction: column;
	 padding: 5px;
	 border: 1px solid #4CAF50;
}

.market-place {
  display: flex;
  align-items: stretch;
  padding: 5px;
  width:100%;
  justify-content: space-around;
  flex-direction: row;
}

.iframe-container > div {
  
  border-radius: 1rem;
}
.amazon-container {
	height: 355px;
	width: 300px;
    border: 1px solid #4CAF50;
	padding: 5px;
	
}

.market-comparision {
	text-align: center;
}
