@media (max-width: 880px) {
	.productWrapper .productImage,
	.productWrapper .productInfo{
		display: block;
		width: 100%;
		margin-bottom: 20px;
		text-align: center;
	}

}

.product {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 2px solid black;
  flex-direction: column; 
}

.productWrapper {
  width: 100%;
  border-left: 2px solid black;
  border-right: 2px solid black;
  display: flex;
  font-family: 'Quicksand', sans-serif;
  padding: 5px;
  flex-direction: row;
}

.productNotFound {
	width: 100%;
	height: 100px;
}

.productDesc {
	padding: 5px;
	width: 100%;
}

.productImage {
	width: 50%;
	padding: 5px;
}

.productInfo {
	width: 50%;
	padding: 5px;
    border: 2px;
}

.productFeatures {
	padding: 5px;
}

.productFeatures ul {
	padding: 5px;
}



.ul feature {
	list-style-type: square;
}

.item-name {
	background-color: #f0f7f0;
}

.stars {
  display: flex;
}
#add-icon{color: orange;}
.fa {
	padding: 10px;
	font-size: 20px;
	width: 50px;
	text-align: center;
	text-decoration: none;
	margin: 5px 2px;
  }
  
  .fa:hover {
	  opacity: 0.7;
  }
  
  .fa-facebook {
	background: #3B5998;
	color: white;
  }
  
  .fa-twitter {
	background: #55ACEE;
	color: white;
  }

  .fa-pinterest {
	background: #cb2027;
	color: white;
  }

  .fa-linkedin {
	background: #007bb5;
	color: white;
  }
  
  .fa-youtube {
	background: #bb0000;
	color: white;
  }
  
  .fa-instagram {
	background: #125688;
	color: white;
  }
  
